import { Product } from './product.model';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class Cart {
  lines: CartLine[] = [];
  itemCount: number = 0;
  cartPrice: number = 0;

  //Add products to cart line
  addItems(product: Product, quantity: number) {
    let item = this.lines.find((lines) => lines.product.id === product.id);
    if (item !== undefined) {
      item.quantity += quantity;
    } else {
      this.lines.push(new CartLine(product, quantity));
    }
    this.recalculate();
  }

  //Update quantity of product
  updateQuantity(product: Product, quantity: number) {
    let item = this.lines.find((lines) => lines.product.id === product.id);
    if (item !== undefined) {
      item.quantity = quantity;
    }
    this.recalculate();
  }

  //Remove productsfrom the lineCart
  removeItem(id: number) {
    let ind = this.lines.findIndex((item) => item.product.id === id);
    this.lines.splice(ind, 1);
    this.recalculate();
  }
  clear() {
    this.lines = [];
    this.itemCount = 0;
    this.cartPrice = 0;
  }
  private recalculate() {
    this.itemCount = 0;
    this.cartPrice = 0;
    this.lines.forEach((line) => {
      this.itemCount += line.quantity;
      this.cartPrice += line.quantity * line.product.price;
    });
  }
}

export class CartLine {
  constructor(public product: Product, public quantity: number) {}
  get totalPrice(): number {
    return this.quantity * this.product.price;
  }
}
