export class Model {
  user: string;
  items: TodoItem[];
  constructor() {
    this.user = 'Deepak M N';
    this.items = [
      new TodoItem('Buy Flowers', false),
      new TodoItem('Get Shoes', true),
      new TodoItem('Collect Tickets', true),
      new TodoItem('Do Workouts', false),
    ];
  }
}

export class TodoItem {
  action: string;
  done: boolean;
  constructor(action, status) {
    this.action = action;
    this.done = status;
  }
}
