import { Component, OnInit } from '@angular/core';
import { Cart } from '../../model/cart.model';
import { Product } from '../../model/product.model';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-cart-details',
  templateUrl: './cart-details.component.html',
  styleUrls: ['./cart-details.component.css'],
})
export class CartDetailsComponent implements OnInit {
  displayedColumns: string[] = [
    'quantity',
    'product',
    'price',
    'subtotal',
    'remove',
  ];
  dataSource;
  constructor(public cartModel: Cart) {}

  ngOnInit(): void {
    this.dataSource = new MatTableDataSource(this.cartModel.lines);
  }

  updateCartList(product: Product, quantity: number) {
    this.cartModel.updateQuantity(product, +quantity);
  }
  renoveFromCart(id: number) {
    this.cartModel.removeItem(+id);
    this.dataSource = this.cartModel.lines;
  }
  applyFilter(event: Event) {
    let filterValue = (event.target as HTMLInputElement).value;
    filterValue = filterValue.trim().toLowerCase();
    this.dataSource = this.cartModel.lines.filter((item) =>
      item.product.name.toLocaleLowerCase().startsWith(filterValue)
    );
  }
}
