import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ShoppingHomeComponent } from './shopping/components/shopping-home/shopping-home.component';
import { CartDetailsComponent } from './shopping/components/cart-details/cart-details.component';
import { CheckoutComponent } from './shopping/components/checkout/checkout.component';
import { AuthgaurdGuard } from './shopping/shared/authgaurd.guard';

const routes: Routes = [
  {
    path: 'home',
    component: ShoppingHomeComponent,
    canActivate: [AuthgaurdGuard],
  },
  {
    path: 'cart',
    component: CartDetailsComponent,
    canActivate: [AuthgaurdGuard],
  },
  {
    path: 'checkout',
    component: CheckoutComponent,
    canActivate: [AuthgaurdGuard],
  },
  { path: '**', redirectTo: '/home' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
