import { Component, OnInit } from '@angular/core';
import { ProductsService } from '../../products.service';
import { Product } from '../../model/product.model';
import { Cart } from '../../model/cart.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-shopping-home',
  templateUrl: './shopping-home.component.html',
  styleUrls: ['./shopping-home.component.css'],
})
export class ShoppingHomeComponent implements OnInit {
  selectedCat = '';
  selectedPage = 1;
  productsPerPage = 4;

  constructor(
    private productService: ProductsService,
    private cartModel: Cart,
    private router: Router
  ) {}
  public get products(): Product[] {
    var pageIndex = (this.selectedPage - 1) * this.productsPerPage;
    return this.productService
      .getProductList(this.selectedCat)
      .slice(pageIndex, pageIndex + this.productsPerPage);
  }

  public get categoryList() {
    return this.productService.getCatagoryList();
  }

  ngOnInit(): void {
    // this.products = this.productService.getProductList(
    //   this.selectedCat,
    //   this.selectedPage,
    //   this.productsPerPage
    // );
    //this.categoryList = this.productService.getCatagoryList();
  }
  changeCategory(category: string) {
    this.selectedCat = category;
    this.changePageNumber(1);
    // this.products = this.productService.getProductList(
    //   this.selectedCat,
    //   this.selectedPage,
    //   this.productsPerPage
    // );
  }
  onPageSizeChange(pageSize: string) {
    this.productsPerPage = +pageSize;
  }

  get pageNumbers(): number[] {
    return new Array(
      Math.ceil(
        this.productService.getProductList(this.selectedCat).length /
          this.productsPerPage
      )
    )
      .fill(0)
      .map((x, i) => i + 1);
  }
  changePageNumber(pageNum: number) {
    this.selectedPage = pageNum;
  }

  addProductsToCart(product: Product) {
    this.cartModel.addItems(product, 1);
    this.router.navigate(['/cart']);
  }
}
