import { Component, OnInit } from '@angular/core';
import { ProductsService } from '../../products.service';
import { Product } from '../../model/product.model';
import { Cart } from '../../model/cart.model';
import { NgForm } from '@angular/forms';
import { ICheckout } from '../../model/checkout.model';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css'],
})
export class CheckoutComponent implements OnInit {
  constructor(private productService: ProductsService, public cart: Cart) {}
  cartLines = [];
  totalGst = 0;
  totalWithGst = 0;
  checkOutDetails: ICheckout;
  testValue = '';
  ngOnInit(): void {
    this.cartLines = this.cart.lines;
    this.totalGst = (this.cart.cartPrice * 12) / 100;
    this.totalWithGst = this.totalGst + this.cart.cartPrice;
  }
  onSubmit(myForm: NgForm) {
    console.log(myForm);
  }
}
