import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-ng-class-demo',
  templateUrl: './ng-class-demo.component.html',
  styleUrls: ['./ng-class-demo.component.css'],
})
export class NgClassDemoComponent implements OnInit {
  constructor() {}

  ngOnInit(): void {}
  buttons = [2017, 2018, 2019, 2020];
  months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug'];
  buttonsObj = this.buttons.map((item) => {
    return { year: item, isActive: false };
  });
  selectedYear = [];

  selectAll() {
    this.buttonsObj.map((item) => {
      item.isActive = true;
    });
  }
  deselectAll() {
    this.buttonsObj.map((item) => {
      item.isActive = false;
    });
  }
  buttonClicked(obj) {
    obj.isActive = obj.isActive ? false : true;
    console.log(obj);
  }
  save() {
    var activeButtons = this.getAtiveButtons();

    console.log(activeButtons);
  }

  getAtiveButtons() {
    var activeButtons = [];
    this.buttonsObj.forEach((item) => {
      if (item.isActive) {
        activeButtons.push(item.year);
      }
    });
    return activeButtons;
  }
  isValidMonth(month) {
    let today = new Date();
    var activeButtons = this.getAtiveButtons();
    if (
      activeButtons.length === 1 &&
      activeButtons[0] === today.getFullYear()
    ) {
      let curMon = today.getMonth();
      if (month > curMon) return true;
    }
    return false;
  }
}
